import VueRouter from 'vue-router';

import loginPage from "@/pages/loginPage";
import ShowPages from "@/pages/ShowPages";

const Route = new VueRouter({
    mode: "history",
    // base: process.env.BASE_URL
    // ,
    routes: [

        {
            path: "/",
            redirect: "/login"
        },
        {
            name: "logingPage",
            path: "/login",
            component: loginPage
        }, {
            name: "showAll",
            path: "/show",
            component: ShowPages
        },
    ]
})
export default Route